import { MainLayoutStore } from '../main-layout/store/main-layout.store';
import { Component, OnInit } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { LayoutPage } from '../main-layout/models/layout-page.model';
import { Router } from '@angular/router';
import { UnitsRouteResolver } from './units-routing.resolver';
import {
    PageHeaderAction
} from '../main-layout/components/page-container/components/page-container-header/page-container-header.component';

@Component({
    selector: 'epop-units-page',
    styleUrls: ['units.page.scss'],
    templateUrl: 'units.page.html'
})
export class UnitsPage extends LayoutPage implements OnInit {

    public pageHeaderActions: Array<PageHeaderAction> = [
        {
            icon: 'mdi/plus',
            onClick: () => undefined,
            position: 'left',
            isDisabled: () => false,
            type: 'big'
        },
        {
            icon: 'mdi/printer',
            onClick: () => undefined,
            position: 'right',
            isDisabled: () => false,
        }
    ];

    constructor(
        private translocoService: TranslocoService,
        mainLayoutStore: MainLayoutStore,
        private router: Router
    ) {
        super(mainLayoutStore);
    }

    ngOnInit(): void {
        this.subscribePageTitle(this.translocoService.selectTranslate('title', {}, 'units'));
    }

    public unitSelected(unitId: number) {
        const path = UnitsRouteResolver.routePath('details', unitId.toString());
        this.router.navigateByUrl(path);
    }
}
