import { StringHelper } from './../../../helpers/StringHelper';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { ColDef, GridApi, GridOptions, GridReadyEvent } from 'ag-grid-community';
import { Observable } from 'rxjs';
import { skipWhile, tap } from 'rxjs/operators';
import { PersonRequest } from 'src/app/common/models/person-request.model';
import { Person } from 'src/app/common/models/person.model';
import { UnitDetails } from 'src/app/common/models/unit-details.model';
import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';
import { CollaboratorCellComponent } from './components/collaborator-cell/collaborator-cell.component';

@Component({
    selector: 'epop-unit-collaborator-list',
    styleUrls: ['unit-collaborator-list.component.scss'],
    templateUrl: 'unit-collaborator-list.component.html'
})
export class UnitCollaboratorListComponent implements OnInit {
    @Input() set setSearchValue(value: string) {
        this.searchValue = value;
        this.updateSearchValue();
    }

    @Output() unitSelected: EventEmitter<number> = new EventEmitter();

    private searchValue: string;

    public unitDetails$: Observable<UnitDetails>;
    public unitDetails: UnitDetails;
    public gridApi: GridApi;

    public frameworkComponents = {
        CollaboratorCellComponent
    };
    public columnDefs: Array<ColDef> = [
        {
            valueGetter: (valParam) => valParam.data as Person,
            cellRenderer: 'CollaboratorCellComponent',
            headerName: this.translateHeader('collaboratorCell'),
            sortable: false,
            resizable: false,
            width: 70,
        },
        {
            field: 'lastName',
            headerName: this.translateHeader('lastName')
        },
        {
            field: 'firstName',
            headerName: this.translateHeader('firstName')
        },
        {
            ...this.generateSimpleTranslatedColDef('gender')
        },
        {
            ...this.generateSimpleTranslatedColDef('service'),
            width: 200,
            suppressSizeToFit: true
        },
        {
            ...this.generateSimpleTranslatedColDef('jobTitle'),
            width: 200,
            suppressSizeToFit: true
        },
        {
            field: 'workingPercentage',
            valueFormatter: valParam => StringHelper.percentageToString(valParam.value),
            headerName: this.translateHeader('rate'),
            comparator: (valueA, valueB) => valueA - valueB
        },
        {
            ...this.generateSimpleTranslatedColDef('skill'),
            width: 180,
            suppressSizeToFit: true
        },
    ];
    public gridOptions: GridOptions = {
        onGridReady: this.onGridReady.bind(this),
        onGridSizeChanged: this.fitGrid,
        rowHeight: 60,
        getRowClass: this.getRowClass,
        suppressContextMenu: true,
        defaultColDef: {
            sortable: true,
            suppressMenu: true,
            resizable: false,
            lockPosition: true
        },
    };
    public unitCollaboratorRows: Array<Person> = [];

    constructor(
        private translocoService: TranslocoService,
        private unitService: UnitService,
        private unitDetailsQuery: UnitDetailsQuery
    ) {}

    ngOnInit() {
        this.unitDetails$ = this.unitDetailsQuery.selectFirst().pipe(
            skipWhile(unitDetails => !unitDetails),
            tap(unitDetails => {
                this.refreshGridData(this.transformData(unitDetails));
            })
        );
        this.unitService.refreshUnits();
    }

    private refreshGridData(newUnitCollaboratorRows: Person[]): void {
        this.unitCollaboratorRows = newUnitCollaboratorRows;
    }

    private onGridReady(params: GridReadyEvent): void {
        this.gridApi = params.api;
        this.gridApi.setRowData(this.unitCollaboratorRows);
        this.gridApi.setSortModel([
            {colId: 'lastName', sort: 'asc'}
        ]);
    }

    private updateSearchValue() {
        this.gridApi?.setQuickFilter(this.searchValue);
    }

    private transformData(unitDetails: UnitDetails): Array<Person> {
        return unitDetails.getPersonRequest()?.map(
            (personReq: PersonRequest) => personReq.getPerson()
        );
    }

    private translateHeader(columnName: string): string {
        return this.translocoService.translate(`units.details.collaborators.tableHeader.${columnName}`);
    }

    private translatePersonValue(columnName: string, value: string): string {
        return this.translocoService.translate(`person.${columnName}.${value}`);
    }

    private getRowClass(params) {
        return params.data.collaboratorCell?.getPersonStatus() === '1' ? 'ag-row--greyed' : '';
    }

    private generateSimpleTranslatedColDef(fieldName: string): ColDef {
        return {
            field: fieldName,
            headerName: this.translateHeader(fieldName),
            // Use value getter (instead of value formatter) to be compatible with the search function :
            valueGetter: (valParam) => this.translatePersonValue(fieldName, valParam.data[fieldName])
        };
    }

    private fitGrid(params) {
        params.api?.sizeColumnsToFit();
    }
}
