import { Component, OnInit } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { LayoutPage } from '../../main-layout/models/layout-page.model';
import { MainLayoutStore } from '../../main-layout/store/main-layout.store';
import { UnitsRouteResolver } from '../units-routing.resolver';
import { UnitService } from 'src/app/common/services/epop-api/units.service';
import { UnitDetailsQuery } from 'src/app/common/store/unitDetails/unit-details.query';
import {
    PageHeaderAction, PageHeaderSearch
} from '../../main-layout/components/page-container/components/page-container-header/page-container-header.component';
import { UnitDetails } from 'src/app/common/models/unit-details.model';

@Component({
    selector: 'epop-details-page',
    styleUrls: ['details.page.scss'],
    templateUrl: 'details.page.html'
})

export class DetailsPage extends LayoutPage implements OnInit {

    public pageHeaderActions: Array<PageHeaderAction> = [
        {
            icon: 'mdi/plus',
            onClick: () => undefined,
            position: 'left',
            type: 'medium',
            isDisabled: () => false,
        },
        {
            icon: 'mdi/printer',
            onClick: () => undefined,
            position: 'right',
            isDisabled: () => false,
        },
        {
            icon: 'mdi/tune',
            onClick: () => undefined,
            position: 'right',
            isDisabled: () => false,
        }
    ];
    public pageHeaderSearch: PageHeaderSearch = {
        placeholder: this.getPlaceholder(),
        onValueChanges: this.updateSearchValue,
        icon: 'mdi/account-search-outline'
    };
    public searchValue: string;
    public currentTabLabel: string;

    constructor(
        private translocoService: TranslocoService,
        mainLayoutStore: MainLayoutStore,
        private route: ActivatedRoute,
        private unitService: UnitService,
        private unitDetailsQuery: UnitDetailsQuery
    ) {
        super(mainLayoutStore);
    }

    ngOnInit() {
        const unitDetailsId = this.route.snapshot.params.unitId;
        this.unitService.refreshUnitDetails(unitDetailsId);
        this.unitDetailsQuery.selectEntity(unitDetailsId).subscribe(u => this.buildPageTitle(u));
    }

    private buildPageTitle(unitDetails: UnitDetails): void {
        if(unitDetails) {
            this.subscribePageTitle(
                this.translocoService.selectTranslate('details.titleWithName', {name: unitDetails.getUnit().getName()}, 'units')
            );
        }
    }

    protected getBackLinkPath(): string {
        return UnitsRouteResolver.baseRoutePath();
    }

    public updateSearchValue(searchValue: string) {
        this.searchValue = searchValue;
    }

    private getPlaceholder(): Observable<string> {
        return this.translocoService.selectTranslate('details.collaborators.placeholder', {}, 'units');
    }
}
